"use client"

import { useEffect, useState } from "react"
import { Chart, registerables } from "chart.js"
import D3ConfusionMatrix from "@/components/d3-confusion-matrix"

Chart.register(...registerables)

interface KPI {
  rowKey: string
  value: string | number
  status?: string
}

interface PlotDataPoint {
  date: string
  value: number
}

interface TableDataPoint {
  id: string
  timePeriod: string
}

export default function Dashboard() {
  // --- FILTER STATES ---
  const [businessUnit, setBusinessUnit] = useState<string>("CCS")
  const [useCase, setUseCase] = useState<string>("CC-Di")
  const [shortCode, setShortCode] = useState<string>("CC-Di-001")
  const [alertKeeperValue, setAlertKeeperValue] = useState<string>("KeeperA")
  const [runtimeValue, setRuntimeValue] = useState<number>(1)
  const [loading, setLoading] = useState<boolean>(true)
  const [currentPeriod, setCurrentPeriod] = useState<string>("May 2025")

  // --- DATA STATES ---
  const [kpis, setKpis] = useState<KPI[]>([])
  const [errorData, setErrorData] = useState<{ plotData: PlotDataPoint[]; tableData: TableDataPoint[] }>({
    plotData: [],
    tableData: [],
  })
  const [referenceMatrix, setReferenceMatrix] = useState<number[][]>([])
  const [currentMatrix, setCurrentMatrix] = useState<number[][]>([])

  // --- HELPERS ---
  const makeLabels = (n: number) => Array.from({ length: n }, (_, i) => i.toString())
  const computeSquareSize = (grid: number[][]) => {
    const maxPx = 300
    const rows = grid.length
    const cols = grid[0]?.length || 0
    if (!rows || !cols) return maxPx
    const cellSize = Math.min(maxPx / rows, maxPx / cols)
    return Math.max(rows, cols) * cellSize
  }

  // --- MOCK DATA ---
  useEffect(() => {
    const fetchData = async () => {
      // Simulate API call
      await new Promise((resolve) => setTimeout(resolve, 1500))

      // Mock KPIs
      setKpis([
        { rowKey: "Accuracy", value: "98.2%", status: "success" },
        { rowKey: "Precision", value: "97.5%", status: "success" },
        { rowKey: "Recall", value: "96.8%", status: "success" },
        { rowKey: "F1 Score", value: "97.1%", status: "success" },
        { rowKey: "Error Rate", value: "1.8%", status: "warning" },
      ])

      // Mock plot data
      setErrorData({
        plotData: [
          { date: "2025-01", value: 2.1 },
          { date: "2025-02", value: 1.9 },
          { date: "2025-03", value: 2.3 },
          { date: "2025-04", value: 1.7 },
          { date: "2025-05", value: 1.8 },
        ],
        tableData: [
          { id: "001", timePeriod: "Class A → Class B" },
          { id: "002", timePeriod: "Class C → Class A" },
          { id: "003", timePeriod: "Class B → Class D" },
        ],
      })

      // Mock confusion matrices
      setReferenceMatrix([
        [42, 2, 1, 0],
        [1, 38, 0, 1],
        [0, 1, 45, 0],
        [0, 0, 2, 37],
      ])

      setCurrentMatrix([
        [40, 3, 2, 0],
        [2, 37, 0, 1],
        [1, 2, 43, 0],
        [0, 1, 3, 35],
      ])

      setLoading(false)
    }

    fetchData()
  }, [])

  // --- RENDER PIE CHART ---
  useEffect(() => {
    if (loading) return
    const ctx = document.getElementById("statusPieChart") as HTMLCanvasElement
    if (!ctx) return

    // Status distribution data
    const statusDistribution = {
      good: 65,
      warning: 25,
      error: 10,
    }

    new Chart(ctx, {
      type: "pie",
      data: {
        labels: ["Good", "Warning", "Error"],
        datasets: [
          {
            data: [statusDistribution.good, statusDistribution.warning, statusDistribution.error],
            backgroundColor: ["rgba(52, 211, 153, 0.8)", "rgba(251, 191, 36, 0.8)", "rgba(239, 68, 68, 0.8)"],
            borderColor: ["rgba(52, 211, 153, 1)", "rgba(251, 191, 36, 1)", "rgba(239, 68, 68, 1)"],
            borderWidth: 1,
          },
        ],
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        plugins: {
          legend: {
            position: "right",
            labels: {
              color: "#e5e7eb",
              font: { size: 14 },
              generateLabels: (chart) =>
                chart.data.labels!.map((label, i) => ({
                  text: `${label}: ${chart.data.datasets![0].data[i]}%`,
                  fillStyle: chart.data.datasets![0].backgroundColor![i] as string,
                  strokeStyle: chart.data.datasets![0].borderColor![i] as string,
                  lineWidth: 1,
                  hidden: false,
                  index: i,
                })),
            },
          },
          tooltip: {
            callbacks: {
              label: (ctx) => `${ctx.label}: ${ctx.raw}%`,
            },
          },
        },
      },
    })
  }, [loading])

  return (
    <div className="min-h-screen bg-gray-950 text-white p-6">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="flex flex-col md:flex-row md:items-center md:justify-between mb-6">
          <h2 className="text-3xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-cyan-400 to-sky-600 mb-2">
            OCTAVE – RG Dashboard
          </h2>
          <p className="text-sky-300 flex items-center gap-2">
            <span className="inline-block h-2 w-2 rounded-full bg-sky-400 animate-pulse" />
            Current Period: {loading ? "Loading…" : currentPeriod}
          </p>
        </div>

        {/* Static Filters & Runtime */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 mb-6">
          {/* Filters (2/3) */}
          <div className="lg:col-span-2 bg-gradient-to-br from-sky-950/40 to-sky-900/20 p-6 rounded-lg border border-sky-800/30 shadow-md">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <p className="text-lg font-medium text-sky-300">Business Unit:</p>
                <p className="text-sky-200">{loading ? "Loading…" : businessUnit || "Not Selected"}</p>
                <p className="mt-4 text-lg font-medium text-sky-300">Use Case:</p>
                <p className="text-sky-200">{loading ? "Loading…" : useCase || "Not Selected"}</p>
              </div>
              <div>
                <p className="text-lg font-medium text-sky-300">Short Code:</p>
                <p className="text-sky-200">{loading ? "Loading…" : shortCode || "Not Available"}</p>
                <p className="mt-4 text-lg font-medium text-sky-300">Alert Keeper:</p>
                <p className="text-sky-200">{loading ? "Loading…" : alertKeeperValue || "Not Selected"}</p>
              </div>
            </div>
          </div>

          {/* Runtime (1/3) */}
          <div className="bg-gradient-to-br from-sky-950/40 to-sky-900/20 p-6 rounded-lg border border-sky-800/30 shadow-md">
            <h3 className="text-lg font-medium text-sky-300 mb-2">Runtime</h3>
            <select
              className="w-full bg-gray-800/80 border border-sky-700/50 rounded-md p-2 text-white focus:ring-2 focus:ring-sky-500"
              value={runtimeValue}
              onChange={(e) => setRuntimeValue(Number(e.target.value))}
            >
              <option value={1}>1</option>
              <option value={2}>2</option>
              <option value={3}>3</option>
              <option value={4}>4</option>
            </select>
          </div>
        </div>

        {/* Confusion Matrices (replacing Plot & Pie) */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 mb-6">
          {/* Confusion Matrices (2/3) */}
          <div className="lg:col-span-2 bg-gray-900/80 rounded-xl shadow-xl overflow-hidden p-6 border border-gray-700/50 backdrop-blur-sm">
            <h2 className="text-2xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-cyan-400 to-sky-600 mb-4">
              Confusion Matrices
            </h2>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {[
                { title: "Reference Matrix", grid: referenceMatrix },
                { title: "Current Matrix", grid: currentMatrix },
              ].map(({ title, grid }, idx) => {
                const side = computeSquareSize(grid)
                return (
                  <div key={idx} className="bg-gray-800/60 rounded-lg p-4 border border-gray-700/50">
                    <h3 className="text-lg font-medium text-sky-300 mb-2 text-center">{title}</h3>
                    {!loading && grid.length > 0 ? (
                      <div className="flex justify-center">
                        <div style={{ width: side, height: side }}>
                          <D3ConfusionMatrix
                            data={grid}
                            labels={makeLabels(grid[0].length)}
                            width={side}
                            height={side}
                          />
                        </div>
                      </div>
                    ) : (
                      <div className="flex items-center justify-center h-48">
                        <svg
                          className="animate-spin h-8 w-8 text-sky-500"
                          xmlns="http://www.w3.org/2000/svg"
                          fill="none"
                          viewBox="0 0 24 24"
                        >
                          <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" />
                          <path
                            className="opacity-75"
                            fill="currentColor"
                            d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                          />
                        </svg>
                      </div>
                    )}
                  </div>
                )
              })}
            </div>
          </div>

          {/* Status Distribution (1/3) */}
          <div className="bg-gradient-to-br from-sky-950/40 to-sky-900/20 p-6 rounded-lg border border-sky-800/30 shadow-md">
            <h3 className="text-lg font-medium text-sky-300 mb-2">Status Distribution</h3>
            <div className="h-80">
              <canvas id="statusPieChart"></canvas>
            </div>
          </div>
        </div>

        {/* KPI Section */}
        <div className="bg-gray-900/80 rounded-xl shadow-xl overflow-hidden p-6 mb-6 border border-gray-700/50 backdrop-blur-sm">
          <h2 className="text-2xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-cyan-400 to-sky-600 mb-4">
            Key Performance Indicators
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-5 gap-4">
            {loading
              ? Array(5)
                  .fill(0)
                  .map((_, i) => (
                    <div
                      key={i}
                      className="bg-gradient-to-br from-sky-950/40 to-sky-900/20 p-4 rounded-lg border border-sky-800/30 shadow-md"
                    >
                      <div className="h-6 bg-sky-800/20 rounded animate-pulse mb-2"></div>
                      <div className="flex items-center">
                        <div className="w-10 h-10 rounded-full bg-sky-800/20 animate-pulse mr-3"></div>
                        <div className="h-6 w-16 bg-sky-800/20 rounded animate-pulse"></div>
                      </div>
                    </div>
                  ))
              : kpis.map((kpi) => (
                  <div
                    key={kpi.rowKey}
                    className="bg-gradient-to-br from-sky-950/40 to-sky-900/20 p-4 rounded-lg border border-sky-800/30 shadow-md transition-all duration-300 hover:shadow-sky-900/20 hover:border-sky-700/50"
                  >
                    <h3 className="text-lg font-medium text-sky-300 mb-2">{kpi.rowKey}</h3>
                    <div className="flex items-center">
                      <div className="w-10 h-10 rounded-full bg-sky-800/40 flex items-center justify-center mr-3">
                        {kpi.status === "success" ? (
                          <svg
                            className="h-5 w-5 text-emerald-400"
                            xmlns="http://www.w3.org/2000/svg"
                            viewBox="0 0 24 24"
                            fill="none"
                            stroke="currentColor"
                            strokeWidth="2"
                            strokeLinecap="round"
                            strokeLinejoin="round"
                          >
                            <path d="M22 11.08V12a10 10 0 1 1-5.93-9.14"></path>
                            <polyline points="22 4 12 14.01 9 11.01"></polyline>
                          </svg>
                        ) : kpi.status === "warning" ? (
                          <svg
                            className="h-5 w-5 text-amber-400"
                            xmlns="http://www.w3.org/2000/svg"
                            viewBox="0 0 24 24"
                            fill="none"
                            stroke="currentColor"
                            strokeWidth="2"
                            strokeLinecap="round"
                            strokeLinejoin="round"
                          >
                            <path d="M10.29 3.86L1.82 18a2 2 0 0 0 1.71 3h16.94a2 2 0 0 0 1.71-3L13.71 3.86a2 2 0 0 0-3.42 0z"></path>
                            <line x1="12" y1="9" x2="12" y2="13"></line>
                            <line x1="12" y1="17" x2="12.01" y2="17"></line>
                          </svg>
                        ) : (
                          <svg
                            className="h-5 w-5 text-rose-500"
                            xmlns="http://www.w3.org/2000/svg"
                            viewBox="0 0 24 24"
                            fill="none"
                            stroke="currentColor"
                            strokeWidth="2"
                            strokeLinecap="round"
                            strokeLinejoin="round"
                          >
                            <circle cx="12" cy="12" r="10"></circle>
                            <line x1="12" y1="8" x2="12" y2="12"></line>
                            <line x1="12" y1="16" x2="12.01" y2="16"></line>
                          </svg>
                        )}
                      </div>
                      <p
                        className={`text-xl font-semibold ${
                          kpi.status === "success"
                            ? "text-emerald-400"
                            : kpi.status === "warning"
                              ? "text-amber-400"
                              : "text-rose-500"
                        }`}
                      >
                        {kpi.value}
                      </p>
                    </div>
                  </div>
                ))}
          </div>
        </div>
      </div>
    </div>
  )
}
